.contact-us {
  background-color: $white;
  padding-block-start: rem(38);
  padding-block-end: rem(39);

  @include tablet {
    padding-block-start: rem(68);
    padding-block-end: rem(57);
  }

  @include desktop {
    @include section-wrapper;
    padding-block-start: rem(71);
    padding-block-end: rem(105);
    display: grid;
    grid-template-columns: rem(175) auto rem(50) rem(527);
    grid-template-rows: auto auto auto;

    grid-template-areas:
      "flag title-c . map"
      ". info-c . map"
      ". btn-c . map";
  }

  &::before {
    @include desktop {
      justify-self: center;
      position: relative;
      display: block;
      width: rem(47);
      height: rem(81);
      content: "";
      background-image: url("/img/icons/flag.svg");
      background-size: contain;
      background-repeat: no-repeat;
      background-position: center;
      grid-area: flag;
      margin-block-start: rem(50)
    }
  }

  &__info-wrapper {
    @include section-wrapper;

    @include desktop {
      max-width: none;
      padding: initial;
      margin: initial;
      margin-block-end: rem(84);
    }

  }

  &__title {
    font-size: rem(25);
    line-height: calc(30 / 25);
    margin-block-end: rem(28);

    @include tablet {
      display: flex;
      margin-block-end: rem(28);
      justify-content: space-between;
      align-items: center;
      font-size: rem(43);
      line-height: calc(50 / 43);
    }

    @include desktop {
      padding-block-start: rem(79);
      justify-content: initial;
      margin-block-end: rem(35);
    }
  }

  &__title::after {
    @include tablet {
      position: relative;
      display: block;
      width: rem(47);
      height: rem(81);
      content: "";
      background-image: url("/img/icons/flag.svg");
      background-size: contain;
      background-repeat: no-repeat;
      background-position: center;
    }

    @include desktop {
      display: none;
    }
  }

  &__contact-info {
    margin-inline-start: auto;
    margin-inline-end: auto;
    margin-block-end: rem(62);

    @include tablet {
      margin-block-end: rem(49);
    }

  }

  &__map {
    margin-block-end: rem(42);

    @include tablet {
      margin-block-end: rem(55);
    }

    @include desktop {
      grid-area: map;
      margin: 0;
    }
  }

  &__write {
    font-size: rem(18);
    line-height: calc(20 / 18);
    margin: 0 rem(30);

    @include tablet {
      font-size: rem(17);
      line-height: calc(20 / 17);
      max-width: rem(230);
      margin: 0 auto;
    }

    @include desktop {
      margin: 0;
      grid-area: btn-c;
    }
  }
}
