    font-size: 1.3333333333rem;
  }
}

.reception__field {
  position: relative;
  padding: 0.8666666667rem 0.6666666667rem;
  margin: 0;
  border: 1px solid #ebebeb;
  background-color: #ffffff;
  color: #000000;
  font-weight: 400;
  font-size: 1.0666666667rem;
  width: 100%;
}
@media (min-width: 768px) {
  .reception__field {
    font-size: 1.3333333333rem;
    padding: 0.9rem 1.3333333333rem;
  }
}

.reception__field:focus-visible {
  outline: 2px solid #444444;
  outline-offset: -2px;
}

.reception__field:hover {
  border-color: #cdcdcd;
}

.reception__field::placeholder {
  font-family: "oswaldR", "Arial", sans-serif;
  font-size: 1.0666666667rem;
  text-transform: uppercase;
  font-weight: 400;
  line-height: 1;
  vertical-align: middle;
  margin-right: 2rem;
}
@media (min-width: 768px) {
  .reception__field::placeholder {
    font-size: 1.3333333333rem;
  }
}

.reception__icon {
  position: absolute;
  top: 50%;
  right: 15px;
  fill: #000000;
  transform: translateY(-50%);
}

.reception:focus-within .reception__icon {
  fill: #68b738;
}

.reception__field--error {
  border-color: #ff8282;
}

.reception__field--error ~ .reception__icon {
  fill: #ff8282;
}

.reception__choose {
  position: absolute;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
}

.reception__thing {
  position: relative;
  display: inline-flex;
  align-items: center;
  width: 2.6666666667rem;
  height: 2.6666666667rem;
  background-color: #ffffff;
  margin-right: 1.0666666667rem;
  border-radius: 4px;
  border: 1px solid #cdcdcd;
}

.reception__thing:hover {
  border-color: #bababa;
}

.reception__thing--radio {
  border-radius: 50%;
}

.reception__thing::after {
  display: inline-block;
  margin: 0 auto;
  content: "";
  opacity: 0;
}

.reception__thing--radio::after {
  width: 0.9333333333rem;
  height: 0.9333333333rem;
  border-radius: 50%;
  border: none;
  background-color: #68b738;
}

.reception__thing--checkbox::after {
  width: 1.3333333333rem;
  height: 0.9333333333rem;
  border-bottom: 3px solid #68b738;
  border-left: 3px solid #68b738;
  transform: rotate(315deg);
}

.reception__choose:disabled + .reception__label {
  opacity: 0.3;
}

.reception__choose:checked + .reception__label > .reception__thing::after {
  opacity: 1;
}

.reception__choose:focus-visible + .reception__label {
  outline: 2px solid #444444;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}